.card-container {
  flex: 1;
  min-width: 300px;
  display: flex;
  flex-direction: column;
  margin-bottom: 2rem;
  text-align: left;
}

.card-link {
  display: block;
  text-decoration: none;
  &:hover {
    opacity: 0.9;
    transform: scale(1.05);
  }
}

.course-card {
  width: 100%;
  height: 20rem;
  border-radius: 20px;
  box-shadow: 0 4px 8px rgba(0, 0, 0, 0.1);
}

.enrollment-bg {
  background: url("https://imgur.com/6n3nCHp.png") center center/cover no-repeat;
}

.news-bg {
  background: url("https://imgur.com/1EoFLTI.png") center center/cover no-repeat;
}

.article-card-img {
  width: 100%;
  height: 20rem;
  object-fit: cover;
  border-radius: 8px;
}

.card-description {
  margin-top: 1.5rem;
  width: 100%;
  p {
    line-height: 1.4;
    text-align: left;
    font-size: 1.1rem;
    font-weight: 900;
    margin-left: 0;
  }
}
